/* App.css */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-family: sans-serif;
  background-color: #f5f5f5;
}

.weighbridge-management-system {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 800px;
  padding: 20px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 20px;
}

.title {
  font-size: 24px;
  margin-bottom: 10px;
}

.company-site {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.company-label,
.site-label {
  font-weight: bold;
  margin-right: 5px;
}

.navigation {
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin-bottom: 20px;
}

.nav-item {
  list-style: none;
}

.nav-button {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.nav-button:hover {
  background-color: #f5f5f5;
}

.transaction-location {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 20px;
}

.transaction,
.location {
  width: 48%;
}

.transaction-button,
.location-button {
  background-color: #4CAF50; /* Green */
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px;
}

/* ... your existing styles ... */

.company-site {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-right: 80%;
}

.dropdown {
  margin-bottom: 10px; /* Add margin for spacing between company and site dropdowns */
}

.company-dropdown,
.site-dropdown {
  width: auto; /* Adjust the width as needed */
}

